import React from 'react'

const Participants = ({ participants, remoteSrc }) => {

	console.log(participants)

	const IDArray = Object.keys(participants)
	
	var users = []

	for (var i = 0; i < IDArray.length; i++) {
		var socketID = IDArray[i]
		users.push(
			<div key={participants[socketID].socketId} className="ParticipantContainer">
				<p className="nickname" 
				style={{ 
					textAlign: 'center',
					marginBottom: '-0.5em',
					fontSize: "1.6em",
					textTransform: "uppercase"
					 }}>{participants[socketID].nickname}</p>
				<p className="userMessage" 
				style={{ 
					textAlign: 'center',
					fontWeight: "300",
					fontSize: "1.5em",
				 }}>{participants[socketID].message}</p>
			</div>)
	}
	
	return (<div key="ParticipantList" className="Participantlist">{users}</div>)

}

// Removed from userMessage style:
// marginBottom: "2.5em"

export default Participants


