import React, { Component } from 'react'
import Participants from './Participants'
import ChatForm from './ChatForm'

import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';

import Video from './Video'

class ChatScreen extends Component {

	componentDidMount() {
		this.props.dispatch({type: 'REQUEST_VIDEO'})
	}

	render() {
		const { localSrc, remoteSrc, participants, dispatch } = this.props
		return(
			<MuiThemeProvider>
				<div id="ChatContainer">
					<div className="VideoContainer">
						{remoteSrc && <Video src={remoteSrc} />}
						{localSrc && <Video src={localSrc} />}
					</div>
					{Object.keys(participants).length > 0 && <Participants participants={participants} />}
					<ChatForm dispatch={dispatch} />
				</div>
			</MuiThemeProvider>
			)
	}
}

export default ChatScreen


