import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import './index.css';

import { createStore, applyMiddleware, compose } from 'redux'
//import ChatApp from './reducers'

// DEVELOPMENT ONLY
import { createLogger } from 'redux-logger'
import ChatApp from './reducers'
import { Provider } from 'react-redux'

import createSocketIoMiddleware from 'redux-socket.io'
import io from 'socket.io-client'
// import { Socket } from 'phoenix'
import channelsMiddleware from './middleware'

// import injectTapEventPlugin from 'react-tap-event-plugin'

// // Needed for onTouchTap
// // http://stackoverflow.com/a/34015469/988941
// injectTapEventPlugin()

//let socketIoMiddleware = createSocketIoMiddleware(socket, "server/");

function pessimisticExecute(action, emit, next, dispatch) {
  emit('action', action);
}

var socketIoMiddleware
var loggerMiddleware
var store
var socket
// let url = 'http://localhost:4000/socket'
// const createChannelsMiddleware = channelsMiddleware(url) 

if (process.env.NODE_ENV === 'development') {
	// socket = io('https://socket-chat-app-server.herokuapp.com');
	socket = io('http://localhost:8080');
	loggerMiddleware = createLogger()
	socketIoMiddleware = createSocketIoMiddleware(socket, "server/", { execute: pessimisticExecute });
	store = createStore(
		ChatApp, 
		compose(
			applyMiddleware(
				socketIoMiddleware,
				// channelsMiddleware('wss://atm-chat.herokuapp.com/socket'),
				channelsMiddleware('ws://localhost:4000/socket'),
				loggerMiddleware
			),
			// window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
	))
}

if (process.env.NODE_ENV === 'production') {
	socket = io('https://socket-chat-app-server.herokuapp.com') // 104.155.150.56:8080
	socketIoMiddleware = createSocketIoMiddleware(socket, "server/", { execute: pessimisticExecute });
	store = createStore(
		ChatApp, 
		compose(
			applyMiddleware(
				socketIoMiddleware,
				channelsMiddleware('wss://atm-chat.herokuapp.com/socket')
			)))
}

ReactDOM.render(
	<Provider store={store}>
 		<App />
	  </Provider>,
  document.getElementById('root')
);



