import React, { Component } from 'react'

//Material UI Component
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import TextField from 'material-ui/TextField';

//import Webcam from 'react-webcam'


class LoginScreen extends Component {
	constructor(props) {
		super(props)
		this.handleSubmit = this.handleSubmit.bind(this)
		//this.formValidate = this.formValidate.bind(this)
	}

	handleSubmit(event) {
		event.preventDefault()
		const { dispatch } = this.props
		// dispatch({type: 'server/set_nickname', nickname: this._name.value}) DOES NOT WORK WITH MATERIAL-UI
		dispatch({type: 'server/set_nickname', nickname: this._name.getValue()})
	}


	// render() {
	// return (
	// <form onSubmit={this.handleSubmit}>
	// 	<input 
	// 		type="text"
	// 		maxLength="12" 
	// 		placeholder="Nickname"
	// 		ref={input => this._name = input}
	// 		autoFocus
	// 		pattern="^[a-zA-Z][a-zA-Z0-9-_\.]{1,20}$"
	// 		required />
	// 	<input type="submit" className="Hiddenloginbutton" />
	// </form>
	// )}

	render() {
		return(
			<div>
			<MuiThemeProvider>
	<form onSubmit={this.handleSubmit}>
		<TextField
		className="LoginScreen"
		// style={{ width: "80%", textAlign: "center" }}
		inputStyle={{ 
			textAlign: "center", 
			color: "white",
			fontSize: "1.3em",
			textTransform: "uppercase"
			 }}
		hintStyle={{ 
			textAlign: "center",
			width: '100%',
		 	fontSize: "1.3em"
		  }}
		underlineStyle={{
			borderColor: "grey"
		}}
		underlineFocusStyle={{
			borderColor: "white"
		}}
		hintText="Nickname"
		maxLength="12"
		fullWidth={false}
		ref={input => this._name = input}
		autoFocus
		pattern="^[a-zA-Z][a-zA-Z0-9-_\.]{1,20}$"
		required /><br />
		<input type="submit" className="Hiddenloginbutton" />
		</form>
		</MuiThemeProvider>
		</div>
	)}

}

/*<Webcam
	className="ownVideo"
	audio={false}
	height="50"
	width="50"
/>*/

export default LoginScreen


