import { Socket } from 'phoenix'

export default function channelsMiddleware(url) {
    
    return store => {
        // Set up connection here
        let activeConnection
        let socket = new Socket(url)
        socket.connect()
        const channel = socket.channel("call", {})
        channel.join()
            .receive("ok", () => { 
                activeConnection = true
                console.log("Successfully joined call channel") })
            .receive("error", () => { console.log("Unable to join") })

        channel.on("webrtc_message", payload => {
            let message = JSON.parse(payload.body)
            if (message.sdp) { // peer will be the host
                gotRemoteDescription(message) // will create answer
            } else { // will only add remote candidate to local pc (peerconnection)
                gotRemoteIceCandidate(message)
            }
        })

        channel.onClose(() => {
            activeConnection = false
            console.log("Channel closed")
        })

        let offerOptions = {
            offerToReceiveAudio: 0,
            offerToReceiveVideo: 1
        }
        // let remoteVideo

        var conf = {iceServers: [{url: 'stun:stun.l.google.com:19302'}]}
        const pc = new RTCPeerConnection(conf)
        console.log("Created local peer connection")

        pc.onicecandidate = event => {
            console.log("onicecandidate")
            if (event.candidate) {
                console.log("Local ICE candidate: \n" + event.candidate.candidate);
                channel.push("webrtc_message", {body: JSON.stringify({
                            "candidate": event.candidate
                        })});
                } else {
                    console.log("event.candidate is empty")
                }
            }

        pc.oniceconnectionstatechange = () => {
            if (pc.iceConnectionState === 'disconnected') {
                store.dispatch({type: 'PEER_DISCONNECT'})
            }
        }

        pc.onaddstream = event => { 
            // Dispatch an action with the stream, to be handled by VideoReducer
            store.dispatch({type: 'SET_REMOTE_STREAM', stream: window.URL.createObjectURL(event.stream)})
            // remoteVideo.src = window.URL.createObjectURL(event.stream)
            console.log("Received remote stream")
        }

        function gotRemoteDescription(description) {
            console.log("Answer from remotePeerConnection: \n" + description.sdp);
            pc.setRemoteDescription(new RTCSessionDescription(description.sdp))
              .then(() => pc.createAnswer(offerOptions))
              .then(gotLocalDescription)
              .catch(handleError)
        }
    
        function gotLocalDescription(description) {
            pc.setLocalDescription(description, () => {
                channel.push("webrtc_message", { body: JSON.stringify({
                    "sdp": pc.localDescription
                })});
            }, handleError)
            console.log("Offer from localPeerConnection: \n" + description.sdp)
        }
    
        function gotRemoteIceCandidate(event) {
            if (event.candidate) {
                console.log("gotRemoteIceCandidate triggered")
                pc.addIceCandidate(new RTCIceCandidate(event.candidate))
                    .catch(handleError)
                console.log("Remote ICE candidate: \n " + event.candidate.candidate);
            }
        }

        function call() {
            // Trust that when a request comes client has already determined user wants to be a host
            if (activeConnection) {
            pc.createOffer(offerOptions)
                .then(offer => pc.setLocalDescription(offer))
                .then(() => {
                    console.log("Offer from localPeerConnection")
                    channel.push("webrtc_message", { 
                        body: JSON.stringify({ "sdp": pc.localDescription })  
                    })})
                .catch(handleError)

            } else { console.log("connection to Phoenix not established yet") }
        }
    
        function handleError(e) { console.log("handleError: " + e) }

        // var localStream
        var localStream
        function requestVideo() {
            const videoOptions = { 
                audio: false,
                video: {
                    width: 60,
                    height: 60,
                    frameRate: { 
                        ideal: 10, 
                        max: 10
                    }
            }}
            console.log("Requesting access to local video and audio");
            navigator.mediaDevices.getUserMedia(videoOptions)
                .then(stream => {
                    console.log("Received local video & audio stream")
                    localStream = stream
                    // Dispatch an action with the stream data for VideoReducer to handle
                    store.dispatch({type: 'SET_LOCAL_STREAM', stream: URL.createObjectURL(stream)})
                })
                .then(() => {
                    console.log("added stream:")
                    console.log(localStream)
                    pc.addStream(localStream)
                })
                .catch(handleError)
        }



        return next => action => {
            // Set up actions here
            switch (action.type) {
                
                case 'REQUEST_VIDEO':
                    requestVideo()
                    break

                case 'CALL':
                    call()
                    break
        
                case 'WEBRTC_MESSAGE':
                    console.log("WEBRTC_MESSAGE")
                    break

                case 'MAKE_CALL':
                    call()
                    break
        
                default:
                    break
            }

            return next(action)
        }
    }
}


