import React, { Component } from 'react'
import ChatScreen from './ChatScreen'
import LoginScreen from './LoginScreen'

class Chat extends Component {
	constructor(props) {
		super(props)
		this.handleRefreshClick = this.handleRefreshClick.bind(this)
	}

	handleRefreshClick(e) {
		e.preventDefault()
	}

	render() {
		const { isHost, localSrc, ownNickname, remoteSrc, participants, dispatch } = this.props
		return (
			<div className="ScreenContainer">
				{ownNickname ? 
					(
						<ChatScreen className="ChatScreen" localSrc={localSrc} remoteSrc={remoteSrc} participants={participants} dispatch={dispatch} />
						) : (
						<LoginScreen className="LoginScreen" dispatch={dispatch} />
						)}
			</div>
		)
	}
}

export default Chat


