import { combineReducers } from 'redux'
import ChatReducer from './ChatReducer'
import NicknameReducer from './NicknameReducer'
import VideoReducer from './VideoReducer'


const ChatApp = combineReducers({
	VideoReducer,
	ChatReducer,
	NicknameReducer
})

export default ChatApp


