function NicknameReducer(state = {
	nickname: null,
	socketID: null
}, action) {

	switch(action.type) {

		case 'set_nickname':
			return Object.assign({}, {
				nickname: action.nickname,
				socketID: action.socketID
				})

		default:
			return state
}}

export default NicknameReducer


