import React, { Component } from 'react';
import TextField from 'material-ui/TextField';

class ChatForm extends Component {

  constructor(props) {
    super(props)
    this.state = {value: ''}
    this.handleChange = this.handleChange.bind(this)
    this.handleSubmit = this.handleSubmit.bind(this)
  }


  handleChange(event) {
    var message = event.target.value
    const maxLength = 120

    if (message.length > maxLength) {
        var newMessage = message.substr(message.length - maxLength)
        console.log(message.length - maxLength)
       this.setState({value: newMessage})
       this.props.dispatch({type: 'server/message', message: newMessage})
    } else {
      this.setState({value: message})
      this.props.dispatch({type: 'server/message', message: message})
  }
  }

  handleSubmit(e) {
    e.preventDefault() //original: e => e.preventDefault()
    this.setState({value: ''})
  }


  render() {
    return (
      <div id="ownMessageFormContainer">
        <form onSubmit={this.handleSubmit}>
          <TextField
            id="ownMessageForm"
            inputStyle={{ 
              textAlign: "center",
              fontSize: "1.3em",
              fontWeight: "400"
                }}
            textareaStyle={{ 
              textAlign: "center", 
              color: "white",
              fontSize: "1.3em",
              fontWeight: "300",
              lineHeight: "1.2em"
                }}
            underlineStyle={{
              borderColor: "grey"
                }}
            underlineFocusStyle={{
              borderColor: "white"
                }}
            fullWidth={true}
            multiLine={true}
            rows={1}
            hintStyle={{ textAlign: "center", width: '100%', fontSize: "1.4em" }}
            underlineShow={true}
            value={this.state.value}
            hintText="Message"
            onChange={this.handleChange}
            autoFocus
      />
      </form>
      </div>
)}
}


//             maxLength="120"

export default ChatForm


