function VideoReducer(state = {isHost: false, peerConnected: false, localSrc: false, remoteSrc: false}, action) {

    switch (action.type) {
        case 'SET_HOST':
            return {
                ...state,
                isHost: action.isHost
            }

        case 'SET_REMOTE_STREAM':
            return {
                ...state,
                remoteSrc: action.stream,
                peerConnected: true
            }

        case 'SET_LOCAL_STREAM':
            return {
                ...state,
                localSrc: action.stream
            }

        case 'PEER_DISCONNECT':
            return {
                ...state,
                peerConnected: false
            }

        default:
            return state
    }
}

export default VideoReducer


